@font-face {font-family: "Material Icons";font-style: normal;font-weight: 400;font-display: block;src: url("../fonts/material-icons.woff2") format("woff2");}
@font-face {font-family: "Material Icons Outlined";font-style: normal;font-weight: 400;font-display: block;}
@font-face {font-family: "Material Icons Round";font-style: normal;font-weight: 400;font-display: block;src: url("../fonts/material-icons-round.woff2") format("woff2");}
@font-face {font-family: "Material Icons Sharp";font-style: normal;font-weight: 400;font-display: block;src: url("../fonts/material-icons-sharp.woff2") format("woff2");}
@font-face {font-family: "Material Icons Two Tone";font-style: normal;font-weight: 400;font-display: block;src: url("../fonts/material-icons-two-tone.woff2") format("woff2");}
.material-icons {font-family: "Material Icons";font-weight: normal;font-style: normal;font-size: 24px;line-height: 1;letter-spacing: normal;text-transform: none;display: inline-block;white-space: nowrap;word-wrap: normal;direction: ltr;-webkit-font-smoothing: antialiased;-moz-osx-font-smoothing: grayscale;text-rendering: optimizeLegibility;}
.material-icons-outlined {font-family: "Material Icons Outlined";font-weight: normal;font-style: normal;font-size: 24px;line-height: 1;letter-spacing: normal;text-transform: none;display: inline-block;white-space: nowrap;word-wrap: normal;direction: ltr;-webkit-font-smoothing: antialiased;-moz-osx-font-smoothing: grayscale;text-rendering: optimizeLegibility;font-feature-settings: "liga";}
.material-icons-round {font-family: "Material Icons Round";font-weight: normal;font-style: normal;font-size: 24px;line-height: 1;letter-spacing: normal;text-transform: none;display: inline-block;white-space: nowrap;word-wrap: normal;direction: ltr;-webkit-font-smoothing: antialiased;-moz-osx-font-smoothing: grayscale;text-rendering: optimizeLegibility;font-feature-settings: "liga";}
.material-icons-sharp {font-family: "Material Icons Sharp";font-weight: normal;font-style: normal;font-size: 24px;line-height: 1;letter-spacing: normal;text-transform: none;display: inline-block;white-space: nowrap;word-wrap: normal;direction: ltr;-webkit-font-smoothing: antialiased;-moz-osx-font-smoothing: grayscale;text-rendering: optimizeLegibility;font-feature-settings: "liga";}
.material-icons-two-tone {font-family: "Material Icons Two Tone";font-weight: normal;font-style: normal;font-size: 24px;line-height: 1;letter-spacing: normal;text-transform: none;display: inline-block;white-space: nowrap;word-wrap: normal;direction: ltr;-webkit-font-smoothing: antialiased;-moz-osx-font-smoothing: grayscale;text-rendering: optimizeLegibility;font-feature-settings: "liga";}
html, body {
    font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
    margin: 0;
    padding: 0;
    height: 100%;
    min-height: 100%;
}
.main,
.main .page {
    height: 100%;
}
.mdc-snackbar {
    bottom: auto;
    top: 35px;
    z-index: 10;
}